/* globals window, document */
import React from 'react';
import PropTypes from 'prop-types';
import {
  Panel,
  PanelHeader,
  Spinner,
  Footer,
  FixedLayout
} from '@vkontakte/vkui';
import moment from 'moment';
import connect from '@vkontakte/vkui-connect';

import formatDoctorCount from '../../utils/formatDoctorCount';
import styles from './Search.module.css';
import DoctorCard from '../organisms/DoctorCard';
import BackButton from '../atoms/BackButton';
import SortTabs from '../moleculs/SortTabs';

const distinct = (value, index, self) => {
  return self.findIndex(({ Id }) => Id === value.Id) === index;
};

class SearchResults extends React.Component {
  componentWillMount() {
    window.addEventListener('scroll', this.onScroll, false);
  }

  // TODO: Избавиться от этой хуйни после рефакторинга
  shouldComponentUpdate(nextProps) {
    const { search: oldSearch } = this.props;
    const { search: newSearch } = nextProps;
    return oldSearch.isLoading !== newSearch.isLoading;
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.onScroll, false);
  }

  onScroll = e => {
    const {
      loadMore,
      search: { isLoading, hasMore }
    } = this.props;
    if (isLoading || !hasMore) {
      return;
    }
    if (
      window.innerHeight + window.scrollY < document.body.scrollHeight &&
      window.innerHeight + window.scrollY >= document.body.scrollHeight - 800
    ) {
      loadMore();
    }
  };

  render() {
    const {
      id,
      search: { title, results, isLoading, description, hasMore },
      onBack,
      query: { date, sort },
      onDoctorSelected,
      onOrder,
      onSortSelected
    } = this.props;

    return (
      <Panel id={id}>
        <PanelHeader left={<BackButton onClick={onBack} />} noShadow>
          {title}
        </PanelHeader>
        <div className={styles.Search}>
          <FixedLayout vertical="top">
            <SortTabs
              value={sort}
              onSelected={newSort => {
                connect.send('VKWebAppScroll', { top: 0, speed: 500 });
                onSortSelected(newSort);
              }}
            />
          </FixedLayout>
          <div className={styles.Description}>
            Расписание варчей на{' '}
            <span className={styles.SearchParams}>{description}</span>
          </div>
          <div className={styles.Results}>
            {results.filter(distinct).map(doctor => (
              <div className={styles.Doctor} key={doctor.Id}>
                <DoctorCard
                  className={styles.DoctorCard}
                  doctor={doctor}
                  date={date || moment().format('YYYY-MM-DD')}
                  onClick={() => onDoctorSelected(doctor)}
                  onOrder={onOrder}
                />
              </div>
            ))}
          </div>
        </div>
        <Footer>
          {isLoading && <Spinner />}
          {!isLoading && !results.length && (
            <div>К сожалению, по вашему запросу ничего не найдено.</div>
          )}
          {!isLoading && results.length > 0 && !hasMore && (
            <div>
              Найдено
              {` ${formatDoctorCount(results.length)}`}
            </div>
          )}
        </Footer>
      </Panel>
    );
  }
}

SearchResults.defaultProps = {
  onBack: null
};

SearchResults.propTypes = {
  id: PropTypes.string.isRequired,
  onBack: PropTypes.func,
  loadMore: PropTypes.func.isRequired,
  search: PropTypes.shape({
    title: PropTypes.string,
    results: PropTypes.arrayOf(PropTypes.object).isRequired,
    isLoading: PropTypes.bool.isRequired,
    description: PropTypes.string.isRequired,
    hasMore: PropTypes.bool.isRequired
  }).isRequired,
  query: PropTypes.shape({
    date: PropTypes.string
  }).isRequired,
  onDoctorSelected: PropTypes.func.isRequired,
  onSortSelected: PropTypes.func.isRequired,
  onOrder: PropTypes.func.isRequired
};

export default SearchResults;
